  padding: 0 18px;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0%;
  vertical-align: middle;
  color: #fff;
  border-radius: 8px;
  background-color: #845186;
  transition: 0.4s;
}
