  }
}
.btn:active {
  background-color: #845186;
}
.btn:disabled, .btn.disabled {
  background-color: #b9b9bd;
  color: #37363b;
}
.btn.white {
  background-color: #fff;
  border: 2px solid #845186;
  color: #845186;
}
@media (hover: hover) {
  .btn.white:hover {
    border: 2px solid #9d749e;
    color: #9d749e;
  }
}
.btn.white.disabled {
  border-color: #b9b9bd;
  color: #323137;
}

