@media (max-width: 1440px) {
  .header__btn {
    height: calc(32px + 16 * (100vw - 1024px) / 416);
    padding: 0 calc(4px + 20 * (100vw - 1024px) / 416);
    font-size: 0 calc(16px + 8 * (100vw - 1024px) / 416);
