}
.header__burger span, .header__burger::after, .header__burger::before {
  content: "";
  height: 3px;
  width: 100%;
  border-radius: 3px;
  background-color: #845186;
  position: absolute;
  left: 0;
}
.header__burger::before {
  top: 0;
}
.header__burger span {
  top: 50%;
  translate: 0 -50%;
}
.header__burger::after {
  bottom: 0;
}

.running-line__item {
  list-style: none;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 55px;
}
.running-line__item a {
