}

.runeline {
  overflow: hidden;
  display: flex;
  align-items: center;
}

.marquee-0 {
  width: 100%;
  height: 56px;
  --space: 0rem;
  display: grid;
  align-content: center;
}
@media (max-width: 568px) {
  .marquee-0 {
    height: 48px;
  }
}

.marquee {
  --duration: 15s;
  --gap: var(--space);
  display: flex;
  user-select: none;
  position: relative;
  max-width: 100vw;
}

.marquee:hover {
  z-index: 99;
}

.marquee__group {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: space-around;
  min-width: 100%;
  animation: scroll var(--duration) linear infinite;
}
.marquee__group a {
  display: flex;
}

@keyframes scroll {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(calc(-100% - var(--gap)));
  }
}
