@media (max-width: 1440px) {
  .home-about__string {
    font-size: 12.2222222222vw;
  }
}
@media (max-width: 1024px) {
  .home-about__string {
    font-size: 15.234375vw;
  }
}
@media (max-width: 568px) {
  .home-about__string {
    font-size: 14.375vw;
  }
}
.home-about__rating {
  display: flex;
}

.home-partner {
  margin-top: var(--sectionMargin);
}
.home-partner__wrapper {
  display: flex;
  flex-direction: column;
  gap: 36px;
}
@media (max-width: 1024px) {
  .home-partner__wrapper {
    gap: 64px;
  }
}
@media (max-width: 568px) {
  .home-partner__wrapper {
    gap: 22px;
  }
}
.home-partner__header {
  display: flex;
  flex-direction: column;
  gap: 29px;
  text-align: center;
  align-items: center;
}
@media (max-width: 568px) {
  .home-partner__header {
    gap: 24px;
  }
}
.home-partner__list {
  display: flex;
  justify-content: space-between;
  gap: 65px;
}
@media (max-width: 1024px) {
  .home-partner__list {
    margin: 0 calc(-1 * var(--container));
    padding: 25px calc(1 * var(--container));
    width: 100vw;
    overflow: auto;
  }
  .home-partner__list li {
    flex: 0 0 155px;
    height: 111px;
  }
}
@media (max-width: 568px) {
  .home-partner__list {
    padding: 12px calc(1 * var(--container));
  }
  .home-partner__list li {
    flex: 0 0 122px;
    height: 88px;
  }
}
.home-partner__list img,
.home-partner__list svg {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.home-us {
  margin-top: var(--sectionMargin);
}
.home-us__wrapper {
  display: flex;
  flex-direction: column;
  gap: 40px;
}
@media (max-width: 568px) {
  .home-us__wrapper {
    gap: 24px;
  }
}
.home-us__header {
  display: flex;
  flex-direction: column;
  gap: 32px;
}
.home-us__main {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}
@media (max-width: 1024px) {
  .home-us__main {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 568px) {
  .home-us__main {
    grid-template-columns: 1fr;
  }
}
.home-us__item {
  display: flex;
  flex-direction: column;
  padding: 24px;
}
@media (max-width: 1024px) {
  .home-us__item {
    box-shadow: 0px 1px 4px 0px rgba(23, 23, 23, 0.2588235294);
    border-radius: 12px;
  }
}
@media (max-width: 568px) {
  .home-us__icon img,
  .home-us__icon svg {
    width: 40px;
    height: 40px;
    flex: 0 0 40px;
  }
}
.home-us__subtitle {
  min-height: 40px;
  margin-top: 28px;
  margin-bottom: 12px;
  display: block;
}
@media (max-width: 568px) {
  .home-us__subtitle {
    min-height: 24px;
    margin: 12px 0;
  }
}
.home-us__subtext {
  margin-bottom: 52px;
  display: block;
  flex: 1;
}
@media (max-width: 568px) {
  .home-us__subtext {
    margin-bottom: 12px;
  }
}
.home-us__link {
  display: flex;
  align-items: center;
  color: #845186;
}